.account-search {
	min-width: 300px;
}

form {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
}

.account-search h3 {
	padding-bottom: 1.5rem;
}

.cci, .phone-number {
	padding: 0 1rem;
}
.cci {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	padding-bottom: 1rem;
	-ms-flex-align: center;
	    align-items: center;
}


.phone-number {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
}

.account-search input {
	padding: .4rem .4rem .4rem .8rem;
	font-size: .9rem;
	background: #8EA0B7;
	border: 0;
	text-transform: uppercase;
	color: #102B4F;
	width: 7rem;
	border-radius: 5px;
	outline: none;
}

input:focus {
	background: lightblue;
}
input[type="submit"]{
	width: 90%;
	margin: 1rem;
}



